<template>
	<view class="box">
		<template v-if="type_check">
			<navigator class="merchants_list" url="/pages/people/list">入驻记录</navigator>
			<image lazy-load class="haibao" :src="imgs" mode="aspectFill"></image>
			<view url="/pages/people/upload" class="bg-btn" @tap="joinIn">
				马 上 开 启
				<!-- #ifdef MP -->
				<button v-if="!is_login" class="confirm-btn" open-type="getUserInfo" @getuserinfo="wxLogin">微信授权登录</button>
				<!-- #endif -->
				</view>
		</template>
		<template v-else>
			<view class="hidden">hello 欢迎</view>
			<image lazy-load class="haibao" src="/static/images/advertise-img.jpg" mode="aspectFill"></image>
		</template>
	</view>
</template>

<script>
	import {
		mapState
	} from 'vuex';
	import jwt_decode from "jwt-decode";
	import {
		getProvider,
		getCode
	} from '@/common/js/login/newlogin.js'
	let provider=""
	export default {
		data() {
			return {
				imgs: this.$webconfig.cloud_url+'/mall/img/merchant/merchant_add.png',
				is_login:false,
			}
		},
		computed: {
			...mapState({
				type_check: state => state.type_check,
				invite_code:state=>state.invite_code
			}),
		},
		onLoad(option) {
			// 通过别人的分享进入
			if(option.invite_code){
				this.$store.commit('saveInviteCode',option.invite_code)
			}
			let local_token=this.$common.getLocalSync('token');
			this.is_login=local_token?true:false;
		},
		onShow() {
			// #ifdef MP
			(async () => {
				//获取服务商信息
				provider = await getProvider();
				//获取code
				this.code = await getCode(provider[0])
			})()
			// #endif
		},
		methods: {
			// 跳转判断
			joinIn(){
				let local_token=this.$common.getLocalSync('token');
				this.is_login=local_token?true:false;
				if(this.is_login){
					uni.navigateTo({
						url:"/pages/people/upload"
					})
				}
			},
			wxLogin(e) {
				// #ifndef H5
				let that = this;
				if (!this.code) {
					uni.showToast({
						icon: 'none',
						title: '正在加载中，稍等一下',
						duration: 1000
					});
				}
				if (e.detail && e.detail.errMsg == 'getUserInfo:ok') {
					uni.getUserInfo({
						success(info_res) {
							that.info_res = info_res;
							that.sendLogin()
						}
					})
				} 
				// #endif
			},
			sendLogin() {
				this.$api.post('/login/save', {
					code: this.code,
					rawData: this.info_res.rawData,
					iv: this.info_res.iv,
					signature: this.info_res.signature,
					encryptedData: this.info_res.encryptedData
				}).then(res => {
					if (res.code == 200) {
						let token = res.data;
						let user_json =jwt_decode(token);
						this.$common.setLocal('token', res.data);
						this.$common.setLocal('customerSeqId', user_json.userId);
						this.$common.setLocal('customerName', user_json.userName || "");
						this.$store.commit('saveUser',user_json)
						this.is_login=true;
						uni.navigateTo({
							url:'/pages/people/upload'
						})
					}
				})
			}
			
		}
	}
</script>

<style lang="scss">
	.merchants_list {
		width: 682rpx;
		height: 60rpx;
		margin: 0 auto;
		font-size: 30rpx;
		color: $color-primary;
		text-align: right;
		font-weight: bold;

	}

	.box {
		padding: 40rpx 0;
		background: white;
		min-height: calc(100vh - var(--window-top) - 80rpx);
		overflow: hidden;
	}

	.haibao {
		width: 682rpx;
		height: 960rpx;
		display: block;
		margin: 0 auto;
		margin-bottom: 60rpx;
	}

	.bg-btn {
		width: 682rpx;
		height: 80rpx;
		line-height: 80rpx;
		padding: 0;
		margin: 0 auto;
		font-size: 26rpx;
		position: relative;
		.confirm-btn {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: red;
			z-index: 10;
			opacity: 0;
		}
	}
</style>
